'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';

export default function NotFound() {
  const router = useRouter();

  useEffect(() => {
    // Log missing path for debugging
    console.warn('[404] Missing route:', window.location.pathname);
    
    // Redirect to desktop after brief delay
    const timer = setTimeout(() => {
      router.push('/');
    }, 2000);

    return () => clearTimeout(timer);
  }, [router]);

  return (
    <div className="fixed inset-0 bg-[#05070a] flex items-center justify-center">
      <div className="text-center">
        <h1 className="text-4xl text-[#00d4ff] mb-4 font-mono">404 - Route Not Found</h1>
        <p className="text-[#aaaaaa] mb-4">Redirecting to desktop...</p>
        <div className="text-[#00ff41] text-sm">Missing route logged to console</div>
      </div>
    </div>
  );
}
